.App {
  text-align: center;
  margin: 20px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.image {
  display: inline-grid;
  width: 300px;
  height: 300px;
  align-self: center;
  margin: 10px;
}

.Answer {
  border-width: 1px;
  margin-top: 8px;
  margin-bottom: 8px;
  border-radius: 5px;
  border-color: #eeeeee;
  border-style: solid;
}

span {
  font-style: italic;
}

.App-header {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

h1 {
  text-align: left;
  font-family: "Proxima Nova",Arial, Helvetica, sans-serif;
  font-size: 1.375rem;
  font-weight: 600;
  letter-spacing: normal;
  word-break: break-word;
  line-height: 1.2;
  display: inline-grid;
}

h2 {
  color: red;
  font-size: calc(20px + 2vmin);
}

.subtitle {
  text-align: left;
}

.finalResult {
  background: white;
  border: #ff00bb;
  border-style: solid;
  border-radius: 5px;
  border-top-width: 40px;
  border-left-width: 20px;
  border-right-width: 20px;
  border-bottom-width: 20px;
  padding: 20px;
  color: black;
  text-align: left;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
